import './myskills.css'
import {DiCss3, DiJavascript1, DiSass, DiGit, DiReact, DiBootstrap} from 'react-icons/di/index'
import { SiNodeDotJs, SiNextDotJs, SiMongodb } from 'react-icons/si/index'
import {AiFillHtml5} from 'react-icons/ai/index'
import {FaNode, FaVuejs } from 'react-icons/fa/index'



const MySkills = () => {
    return (
        <div className="my-skills" id="skills">
            <div className="container">
                <h2>My Skills</h2>
                <div className="skills-icons" >
                    <div>
                        <p>HTML</p>
                        <AiFillHtml5 className="icons"/>
                    </div>

                    <div>
                        <p>CSS</p>
                        <DiCss3 className="icons"/>
                    </div>
                    <div>
                        <p>JAVASCRIPT</p>
                        <DiJavascript1 className="icons"/>
                    </div>
                    <div>
                        <p>SASS</p>
                        <DiSass className="icons"/>
                    </div>
                    <div>
                        <p>GIT</p>
                        <DiGit  className="icons"/>
                    </div>
                    <div>
                        <p>Node JS</p>
                        <FaNode className="icons" />
                    </div>
                    <div>
                        <p>Express JS</p>
                        <SiNodeDotJs className="icons" />
                    </div>
                    
                    <div>
                        <p>Mongo DB</p>
                        <SiMongodb className="icons" />
                    </div>
                    

                    <div>
                        <p>Next JS</p>
                        <SiNextDotJs className="icons" />
                    </div>
                    
                    
                    <div>
                        <p>REACT JS </p>
                        <DiReact className="icons" />
                    </div>
                    <div>
                        <p>VUE JS </p>
                        <FaVuejs className="icons" />
                    </div>

                    <div>
                        <p>BOOTSTRAP</p>
                        <DiBootstrap className="icons" />
                    </div>
                    
                    
                    
                </div>
            </div>
            
        </div>
    )
}

export default MySkills
