import React from 'react'
import Nav from './Nav/Nav'
import './header.css'
import Typewriter from 'typewriter-effect'

const Header = () => {
    return (
        <header style={{backgroundImage:'url("image/back.jpg")', backgroundPosition:'center', backgroundSize:'cover'}}>
            
            <div className="container">
                <div className="header-top" >
                    <img  className="logo" src="image/logo.png" alt="logo"/>
                    <Nav/>
                </div>
                <div className="header-bottom">
                    <img src="image/nabil.jpg" alt="profile"/>

                    <h2>
                        <Typewriter onInit={(typewriter)=> {
                                typewriter.typeString('Hi My Name is Awad Ahmed Nabil').pauseFor(1000).deleteAll().typeString('My code is  much simpler than my name :)').start()
                        }}/>
                    </h2>

                    <a href="#about"><button className="btn">More</button></a>
                </div>
            </div>

            
            <div className="overlay"></div>

        </header>
    )
}

export default Header
