import { AiOutlineMail, AiOutlinePhone } from 'react-icons/ai'
import emailjs from 'emailjs-com';
import { GrLocation } from 'react-icons/gr/index'
import './contact.css'

const Contact = () => {

    function sendEmail(e) {
        e.preventDefault();
    
        emailjs.sendForm('service_3d17dze', 'template_bh4catg', e.target, 'user_VewzXfBwjsa3h4ylb6zD5')
          .then((result) => {
              console.log(result.text);
          }, (error) => {
              console.log(error.text);
          });
          e.target.reset()
      }
    return (
        <div className="contact-me" id="contact">
            <div className="container">
            <h2>Contact Details</h2>
            <div className="contact-area">

                <div className="contact-info">
                    <h3>Contact Information</h3>
                    <p><GrLocation/> Bucharest/Romania </p>
                    <p><AiOutlineMail/> 
                        <a href="mailto:Nazar32341@gmail.com">
                            Nazar32341@gmail.com
                        </a> 
                    
                    </p>

                    <p><AiOutlinePhone/> 
                        <a href="tel:0040737189138">
                            +40737189138
                        </a> 
                    
                    </p>
                    
                </div>
                
                <form name="contact" method="POST" onSubmit={sendEmail}>
                        <h3>Get In Touch</h3>
                        <input type="text" placeholder="Your Name" name="name"/>
                        <input type="text" placeholder="Email" name="email"/>
                        <textarea placeholder="Write you Message" name="message"/>
                        <button type="submit">Send</button>

                </form>
            </div>
            </div>
            
        </div>
    )
}

export default Contact
