import './about.css'
import {FcSearch} from 'react-icons/fc/index'
import {FaRegSmileWink} from 'react-icons/fa/index'

const About = () => {
    return (
        <div className="about-me article" id="about" >
            <div className="container">
                <h2>A little bit About me  <FaRegSmileWink className="smile-icon"/> </h2>
                
                <FcSearch className="about-icon"/>

                <div className="about-first">
                <p>My name is Nabil Awad. I'm  half Romanian and half Sudanese 
                    I am a Junior self-taught web deeloper based in Bucharest, Romania.
                    I create beautiful professional websites using best practice accessibility.
                    I enjoy turning complex problems into simple, beautiful and intuitive interface designs.
                    
                </p>
                <p>I am passionate about transforming lives through tech.
                    I believe tech should ultimately make life easier
                </p>
                <p> If you are looking for a web developer to help bring your ideas to life,
                    work on an existing project, or join a team of developers?
                    I am currently available to take on any project.
                </p>
                
                </div>
            </div>
            
        </div>
    )
}

export default About
